import {request} from './request'

//获得袋子采购单
export async function GetAllBags(data){
    return await request({
        method:'POST',
        url:'/api/BusinessUnit/GetAllBags',
        data:data
    })
}
//增加更新袋子采购单
export async function InsertOrUpdateBag(data){
    return await request({
        method:'POST',
        url:'/api/BusinessUnit/InsertOrUpdateBag',
        data:data
    })
}
//删除袋子采购单
export async function DeleteBag(data){
    return await request({
        method:'DELETE',
        url:'/api/BusinessUnit/DeleteBag',
        data:data
    })
}
//获得相关ID袋子采购单
export async function GetRelatedBags(data){
    return await request({
        method:'POST',
        url:'/api/BusinessUnit/GetRelatedBags',
        data:data
    })
}